<template>
    <v-container fluid>
        <div v-if="Object.keys(tags).length===0 && !loading">暂无数据</div>
        <div v-else>
            <v-card class="pa-2">
                <div class="d-flex justify-space-between">
                    <v-card-title class="pt-0 pb-0 ps-2">标签</v-card-title>
                    <v-card-title class="pt-0 pb-0 pe-2">
                        共<span class="display-1 d-inline primary--text">{{Object.keys(tags).length}}</span>个
                    </v-card-title>
                </div>
                <v-chip-group column multiple active-class="primary--text" v-model="selectedTags">
                    <div v-if="loadingTags">
                        <v-skeleton-loader type="chip"/>
                    </div>
                    <div v-else>
                        <v-chip filter filter-icon="mdi-tag" v-for="tag in Object.keys(tags)" :key="tag">
                            {{tag+" ("+tags[tag]+")"}}
                        </v-chip>
                    </div>
                </v-chip-group>
            </v-card>
            <ArticlePreview @transferCurrentPage="getCurrentPage" :blog-articles="blogArticles" :total-page="totalPages"
                            :loading="loading"/>
        </div>
    </v-container>
</template>

<script>
    import ArticlePreview from "@/components/ArticlePreview";

    export default {
        name: "Tag",
        components: {ArticlePreview},
        props: ["paramTag"],
        data: () => ({
            tags: [],
            selectedTags: [0],
            selectedTagsList: [],
            blogArticles: [],
            totalPages: 0,
            loading: true,
            loadingTags: true
        }),
        methods: {
            getAllTags() {
                let _this = this
                this.axios.get(this.API.URL.QUERY_TAG_WITH_COUNT, {headers: {'Content-Type': 'multipart/form-data'}})
                    .then(function (result) {
                        _this.loadingTags = false
                        let data = result.data
                        if (data.success) {
                            _this.tags = data.data

                            let keys = Object.keys(data.data)
                            _this.selectedTagsList = []
                            _this.selectedTagsList.push(keys[0])
                            _this.getArticles(1)
                        }
                        console.log(result)
                    }, function (error) {
                        _this.loadingTags = false
                        console.log(error)
                    })
            },
            getArticles(currentPage) {
                this.loading = true
                let _this = this
                this.axios.get(this.API.URL.QUERY_ARTICLE_BY_TAGS + "&tags=" + this.selectedTagsList + "&page=" + (currentPage - 1), {headers: {'Content-Type': 'multipart/form-data'}})
                    .then(function (result) {
                        _this.loading = false
                        let data = result.data
                        if (data.success) {
                            console.log(data)
                            _this.totalPages = data.data.totalPages
                            _this.blogArticles = data.data.data
                            console.log("_this.blogArticles", _this.blogArticles)
                        }
                    }, function (error) {
                        _this.loading = false
                        console.log(error)
                    })
            },
            getCurrentPage(currentPage) {
                this.getArticles(currentPage)
                console.log("从ArticlePreview传过来的当前page为", currentPage)
            }
        },
        mounted() {
            this.getAllTags()
        },
        watch: {
            selectedTags() {
                if (this.selectedTags.length !== 0) {
                    console.log(this.selectedTags)
                    console.log(this.tags)
                    let keys = Object.keys(this.tags)
                    this.selectedTagsList = []
                    for (let i = 0; i < this.selectedTags.length; i++) {
                        this.selectedTagsList.push(keys[this.selectedTags[i]])
                    }
                    this.getArticles(1)
                }
            },
            paramTag: {
                handler() {
                    //避免首次获取不到
                    setTimeout(() => {
                        console.log("paramTag", this.paramTag)
                        if (this.paramTag !== undefined) {
                            let keys = Object.keys(this.tags)
                            for (let i = 0; i < keys.length; i++) {
                                if (keys[i] === this.paramTag) {
                                    console.log(i, keys[i], this.paramTag)
                                    this.selectedTags = []
                                    this.selectedTags.push(i)
                                    break
                                }
                            }
                        }
                    }, 300)
                },
                // 首次进入页面即执行
                immediate: true
            }
        }
    }
</script>

